class Par extends Component {

  makeParText(textProp) {
    var text = textProp;
    if (Array.isArray(text)) {
      text = text.map(function(line) {
        var res = line;
        if (line.indexOf("@") > -1) {
          res = <span className="italic">{line}</span>;
        }
        return res;
      });
      text = [].concat.apply([], text.map(line => [line, <br/>]));
    }
    return text;
  }

  componentWillReceiveProps(newProps) {
    var text = this.makeParText(newProps.text);
    this.setState({
      fontSize: newProps.fontSize,
      text: text
    });
  }

  toggleHover() {
    this.setState({ hovered: !this.state.hovered });
  }

  constructor(props) {
    super(props);
    var text = this.makeParText(props.text);
    this.state = {
      fontSize: props.fontSize,
      text: text,
      hovered: false
      // CAN ADD ADDITIONAL STYLE OBJECT IF NEED BE!
    };
    this.toggleHover = this.toggleHover.bind(this);
  }

  render() {
    var parStyle = ('zeroHeight' in this.props)
      ? {
          fontSize: this.state.fontSize,
          paddingTop: this.state.fontSize,
          paddingLeft: this.state.fontSize,
          paddingRight: this.state.fontSize,
          lineHeight: this.state.fontSize * 1.6 + 'px',
          height: 0
        }
      : {
        fontSize: this.state.fontSize,
        paddingTop: this.state.fontSize,
        paddingLeft: this.state.fontSize,
        paddingRight: this.state.fontSize,
        lineHeight: this.state.fontSize * 1.6 + 'px'
      };
    if (this.props.underline && this.state.hovered) {
      parStyle['textDecoration'] = 'underline';
    }
    return(
      <Box style={{width: '100%'}}>
        <div onMouseEnter={this.toggleHover} onMouseLeave={this.toggleHover}
          className={classNames("par")}
          style={parStyle}>
          {this.state.text}
        </div>
      </Box>
    )
  }
}

class App extends Component {

  componentDidMount() {
    this.updateWindowDimensions();
    window.addEventListener('resize', this.updateWindowDimensions);
    this.setState({
      mounted: true
    })
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updateWindowDimensions);
  }

  setSelectedRole(e) {
    var role = e.target.getAttribute('role');
    this.setState({
      selectedRole: role,
      pages: this.makePages(this.state.parFontSize, role)
    });
  }

  updateWindowDimensions() {
    var windowWidth = window.innerWidth;
    var windowHeight = window.innerHeight;
    console.log(windowWidth);
    var parFontSize = 24;
    switch (true) {
      case windowWidth > 1200:
        parFontSize = 42;
        break;
      case windowWidth > 800:
        parFontSize = 30;
        break;
      default:
        parFontSize = 25;
        break;
    }
    this.setState({
      windowWidth: windowWidth,
      windowHeight: windowHeight,
      parFontSize: parFontSize,
      pages: this.makePages(parFontSize),
      position: {
        x: 2*windowWidth/5,
        y: windowHeight/2
      }
    });
  }

  makeTextStyle(fontSize, fontRatio, indent, lineHeightRatio) {
    var size = fontRatio ? fontSize * fontRatio : fontSize;
    var indentation = indent ? size * 2 : 0;
    var lineHeight = lineHeightRatio
      ? size * lineHeightRatio + 'px'
      : size * 1.6 + 'px';
    return({
      fontSize: size,
      lineHeight: lineHeight,
      textIndent: indentation
    });
  }

  constructor(props) {
    super(props);
    this.setClicked = this.setClicked.bind(this);
    this.imageWidth = 2800;
    this.state = {
      selectedRole: Object.keys(roleDescriptions)[0],
      isOpen: false,
      dropdownOpen: false,
      isPositionOutside: true,
      position: {
        x: 0,
        y: 0
      },
      clicked: false,
      windowWidth: 0,
      windowHeight: 0,
      hatHeight: 0,
      parFontSize: parFontMin,
      logoFontSize: logoFontMin,
      pages: this.makePages(),
      mounted: false
    }
    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
    this.updateHatHeight = this.updateHatHeight.bind(this);
    this.makePages = this.makePages.bind(this);
    this.makeRoles = this.makeRoles.bind(this);
    this.setClicked = this.setClicked.bind(this);
    this.setSelectedRole = this.setSelectedRole.bind(this);
    this.scrollToFirstSlide = this.scrollToFirstSlide.bind(this);
    // this.makeTextStyle = this.makeTextStyle.bind(this);
  }

  makeRoles(fontSize, role) {
    var roleDivs = [];
    var selRole = role ? role : this.state.selectedRole;
    var textStyle = this.makeTextStyle(fontSize, 3/5, false, 2);
    for (var role in roleDescriptions) {
      var css = (role === selRole)
        ? classNames("role", "selected-role")
        : "role";
      var roleStyle = (role === selRole)
        ? this.makeTextStyle(fontSize, 3/5, true, 2)
        : textStyle;
      roleDivs.push(
        <div style={roleStyle}
          className={css}
          role={role}
          onClick={this.setSelectedRole}>
          {role}
        </div>
      );
    }
    return(
    <div style={{width: '100%', height: '100%'}}>
      <Box style={{height: 0}}>
        <Par fontSize={fontSize} zeroHeight text="Roles in our collective:"/>
      </Box>
     <Flex column justify='center' align='center' style={{width: '100%', height: '100%'}}>
      <Flex justify='center' align='flex-start'>
        <Flex column w={1/4}>
          {roleDivs}
        </Flex>
        <Flex column w={2/4}>
          <Box w='100%'>
            <div className="par" style={this.makeTextStyle(fontSize, 1, false)}>{roleDescriptions[selRole].specs}</div>
          </Box>
          <Box w='100%'>
            <div style={{width: '100%', height: 0, margin: fontSize + "px 0"}} className="line"/>
          </Box>
          <Box w='100%'>
            <div className="par" style={this.makeTextStyle(fontSize, 1, false)}>{roleDescriptions[selRole].values}</div>
          </Box>
        </Flex>
       </Flex>
      </Flex>
      </div>
    );
  }

  makePages(fontSize, role) {
    let roles = this.state
      ? this.makeRoles(fontSize, role)
      : <div/>;
    let pages = [];
    var lineOne = "INTACT " + String.fromCharCode(8212) + " web platform that supports victims of abuse in personal relationships. It contains free and discrete tools for understanding of an abusive situation and finding change. Intact tackles emotional, physical, verbal, sexual, and psychological abuse.";
    var lineTwo = "CREATE FOR SOCIAL CHANGE (CFSC) " + String.fromCharCode(8212) + " full-credit group independent study. We make content that goes live on Intact. Spring 2017 is our second session.";
    pages.push(
      <section ref={(section) => {this.FirstSlide = section}} className="first-slide">
       <Flex column justify='flex-start' style={{width: '100%', height: '100%'}}>
        <Box style={{flexGrow: 1}}>
        <Par fontSize={fontSize} text={lineOne}/>
        <Par fontSize={fontSize} text={lineTwo}/>
        </Box>
        <Box style={{flexGrow: 1, alignSelf: 'flex-end'}}>
         <a className="mailto" href={"mailto:elena_kashtelyan@brown.edu"}>
          <Par fontSize={fontSize} underline text="REACH OUT"/>
          </a>
        </Box>
       </Flex>
      </section>
    );
    pages.push(
      <section className="second-slide">
        <Flex column justify='flex-start' style={{width: '100%', height: '100%'}}>
          <Par fontSize={fontSize} text="
            INTACT has two components: organization and education.
          "/>
          <Par fontSize={fontSize} text="
            Organization entails helping users collect information relevant to their situation in a meaningful way, through specified secure journaling tool. The tool is in progress, built by students in CFSC.
          "/>
          <Par fontSize={fontSize} text="
            Education entails providing users with knowledge they need, in accessible form of short articles and videos. Much of that we are making ourselves, in CFSC.
          "/>
        </Flex>
      </section>
    );

    pages.push(
      <section className="third-slide">
        {roles}
      </section>
    );

    pages.push(
      <section className="fourth-slide">
        <Flex column justify='flex-start' style={{width: '100%', height: '100%'}}>
          <Par fontSize={fontSize} text="
            Aside from these roles, we will gather weekly as a collective and often meet with professionals working in the field.
          "/>
          <Par fontSize={fontSize} text="
             Our faculty sponsor, Dr. Lindsay Orchowsky, is affiliated with the BioMed department, so the GISP is registered as BIO1950.
          "/>
          <Par fontSize={fontSize} text="
            Come to our first session and hear about everything in detail! First meeting time TBA, check back here in January.
          "/>
          <Par fontSize={fontSize} text={[
            "If you are interested, please email Lena:",
             "elena_kashtelyan@brown.edu"]}/>
        </Flex>
      </section>
    );
    pages.push(<div/>);
    return pages;
  }

  scrollToFirstSlide() {
    console.log(this.refs);
    if (this.state) {
      animateScroll.scrollTo(this.state.windowHeight);
    }
  }

  updateHatHeight(height) {
    this.setState({
      hatHeight: height,
      logoFontSize: Math.floor( logoFontMin * height / minHatSize * Math.pow(0.94, (height / minHatSize-1)) )
    });
  }

  setClicked(e) {
    console.log('setting spotlight clicked');
    this.setState({
        clicked: !this.state.clicked,
    });
  }

  render() {

    var spotlight_style = {
      // width: this.imageWidth,
      top: this.state.position.y,
      left: this.state.position.x
    };

    var fullPageOptions = {
      slides : this.state.pages,
      scrollSensitivity: 3,
      touchSensitivity: 7,
      scrollSpeed: 400,
      hideScrollBars: true,
      enableArrowKeys: true,
      breakpoint: 375
    };

    let fullPage = this.state.mounted
      ? <Fullpage {...fullPageOptions} />
      : this.state.pages;

    let downArrow = ( this.state.hatHeight === this.state.windowHeight )
      ? <img className="down-arrow" alt="start" src={downArrowIcon} onClick={console.log('clicked arrow')}/>
      : <div/>;

    let spotlight = this.state.mounted
      ? <div className={classNames('topbar-spotlight-wrapper')}
          style={{height: this.state.hatHeight-1}}>
          <div style={spotlight_style} className='landing-spotlight' onClick={this.setClicked}/>
        </div>
      : <div/>;

    return (
      <div>

        {fullPage}

        <div style={{position: 'fixed', top: this.state.hatHeight/2, zIndex: 29}}>
          <div style={{height: 0}}>
            <div className="intact-logo" style={{fontSize: this.state.logoFontSize, marginLeft: this.state.windowWidth/2, transform: 'translate(-50%, -50%)'}}>
              intact
            </div>
          </div>
        </div>

        <div ref={(node) => this.Node = node} onClick={this.scrollToFirstSlide}
          style={{position: 'fixed',
            top: this.state.hatHeight*0.95,
            left: '50vw',
            transform: 'translate(0, -50%)',
            zIndex: 29}}>
              {downArrow}
        </div>

        <div style={{position: 'fixed', top: '0px', zIndex: 28}}>

          {spotlight}

          <div>
            <TrackDocument formulas={[getDocumentElement, getDocumentRect, calculateScrollY,
                                 topTop, topBottom, topCenter, centerCenter, bottomBottom,
                                 bottomTop]}>
              {(documentElement, documentRect, scrollY, topTop,
                topBottom, topCenter, centerCenter, bottomBottom, bottomTop) =>
                  <TrackedDiv formulas={[topCenter]}>
                    {(posTopCenter) =>
                    <div style={{background: '#FFFFFF', borderBottom: '1px solid black'}}>
                    <ReactHeight onHeightReady={height => this.updateHatHeight(height)}>
                      <section
                        className="landing-slide"
                        proxy="scale-proxy"
                        style={tween(scrollY, [
                          [ 0, { transform: scale(1, 1), opacity: 1, height: px(this.state.windowHeight) } ],
                          [ this.state.windowHeight, { transform: scale(1, 1), opacity: 0, height: px(58)  } ]
                        ], easeOutBounce)}>
                      </section>
                    </ReactHeight>
                    </div>}
                  </TrackedDiv>}
              </TrackDocument>
            </div>
          </div>

      </div>
    );
  }
}

